import cityData from '@/componets/uni/city/city-data/city.data.js'
import mpvuePicker from '@/componets/uni/city/mpvuePicker.vue'
export default {
	name: 'indexHeader',
	props:{
		scrollTop: {
			type: Number,
			default: 0
		},
		currentCity: {
			type: String,
			default: '济南市'
		}
	},
	components:{
		mpvuePicker
	},
	watch: {
		scrollTop (newVal, oldVal) {
			if (newVal > 0) {
				this.opacity = Math.min(1, newVal / 140)
			} else {
				this.opacity = 0
			}
		}
	},
	computed: {
		textColor () {
			return this.scrollTop === 0 ? '#fff' : '#333'
		}
	},
	data() {
		return {
			opacity: 0,
			pickerValueDefault: [0, 0],
            pickerValueArray:cityData
		}
	},
	methods: {
		search () {
			uni.navigateTo({
				url: '/pages/search/Search'
			})
		},
		citySelect () {
			this.$refs.mpvuePicker.show()
		},
		onConfirm (data) {
			this.$emit('selectedCity', data)
		},
		switchMine () {
			uni.switchTab({
				url: '/pages/mine/index'
			})
		}
	}
}
